.button {
    border-radius: 5px;
    width: 78px;
    height: 36px;
}

.disabled {
    background: rgba(0, 77, 153, 1);
    background: grey;
}

.secondary {
    color: blue;
    background-color: transparent;
    width: 176px;
    height: 30px;
    border: blue solid 3px;
}

.default {
    color: white;
    background-color: royalblue;
    border-radius: 3px;
}

.red {
    color: white;
    background-color: red;
    border-radius: 3px;
}

.default:hover {
    border: rebeccapurple;
}

.red:hover {
    border: rebeccapurple;
}

.secondary:hover {
    border: rebeccapurple;
}


